@import '~vux/dist/components/divider/style.css';#play{background-color:#fff;-webkit-user-select:none;user-select:none;-webkit-touch-callout:none;-khtml-user-select:none;-moz-user-select:none;-ms-user-select:none}#play .toggle-set{position:relative;padding:15px;overflow:hidden;overflow-y:scroll;background-color:#fff;color:#333;font-size:14px}#play .toggle-set li{display:inline-block;width:18%;height:60px;margin:5px 1%;border:1px solid #d7d7d7;text-align:center;line-height:60px;border-radius:4px;box-sizing:border-box}#play .toggle-set li:active,#play .toggle-set li.active{box-shadow:0 0 20px rgba(53,52,51,0.5) inset;color:#999}#play iframe{position:relative;z-index:99}#play .show-state{position:absolute;top:20px;left:50%;width:100px;margin-left:-25px;color:#333;z-index:9}


